import uniSection from '../../components/uni-section/uni-section.vue';
import { request } from '../../utils/request.js';
export default {
	components: { uniSection },
	data() {
		return {
			// 微信昵称
			nickname: '',
			center: {},
			// 主播数
			num: '',
			// 总收益
			all: '',
			// 可提现金额
			ablemoney: '',
			// 提现模态框的显示隐藏
			showMonmodal: false,
			// 用户信息
			userinfo: {},
			token: '',
			form: {
				cashMoney: ''
			}
		};
	},
	onLoad() {
		this.getToken();
		this.getuserInfo();
	},
	onShow() {
		this.getMineinfo();
	},
	methods: {
		getToken() {
			this.token = uni.getStorageSync('token');
		},
		getMineinfo() {
			const that = this;
			request({
				url: '/api/user/userCenter'
			}).then(res => {
				// if(res.data.data.code == 1){
				that.ablemoney = res.data.data.able_money.toString();
				that.num = res.data.data.anchor_num.toString();
				that.all = res.data.data.money;
				// }
			});
		},
		getuserInfo() {
			this.userinfo = uni.getStorageSync('userinfo');
		},
		getMoney() {
			this.showMonmodal = true;
		},
		handleContact(e) {
			console.log(e.detail.path);
			console.log(e.detail.query);
		},
		closeModal() {
			this.showMonmodal = false;
		},
		// 关于模态框的函数
		withdraw() {
			const that = this;
			console.log(that.form.cashMoney)
			console.log(that.ablemoney)
			console.log(that.form.cashMoney <= that.ablemoney)
			if(that.form.cashMoney <= that.ablemoney){
				request({
					url: '/api/user/applyWithdraw',
					method: 'POST',
					data: {
						money: that.form.cashMoney
					}
				}).then(res => {
					if(res.code == 1){
						uni.showToast({
							title:res.data.msg,
							icon:"success"
						})
					}else{
						uni.showToast({
							title:res.data.msg
						})
					}
					that.getMineinfo()
					that.form.cashMoney = ""
				});
			}else{
				uni.showToast({
					title:"超出可提现金额，请重新填写",
					icon:"none"
				})
				setTimeout(()=>{
					this.showMonmodal = true
				},2000)
			}
		}
	}
};
